@use '../utils' as *;

/*----------------------------------------*/
/*  19. product CSS START
/*----------------------------------------*/
.product {
    &__plr {
        padding: 0 200px;

        @media #{$xxxl} {
            padding: 0 100px;
        }

        @media #{$xxl} {
            padding: 0 100px;
        }

        @media #{$xl,$lg,$md} {
            padding: 0 30px;
        }

        @media #{$xs} {
            padding: 0;
        }
    }
    &__title-sm {
        & h4 {
            font-size: 22px;
            font-weight: 700;
            padding-bottom: 40px;

            @media #{$md} {
                font-size: 20px;
            }

            @media #{$xs} {
                font-size: 20px;
            }
        }
    }
    &__item {
        background-color: var(--it-common-white);

        &:hover {
            & .product__img {
                &::after {
                    opacity: 1;
                    visibility: visible;
                }
            }
            
            & .product__icon {
                opacity: 1;
                visibility: visible;
                bottom: 40%;
            }
        }
    }
    &__img {
        position: relative;

        & img {
            width: 100%;
            height: 332px;
        }

        &::after {
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(41, 41, 48, 0.6);
            position: absolute;
            opacity: 0;
            visibility: hidden;
            transition: 0.3s;
        }
    }
    &__icon {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0px 10px;
        z-index: 2;
        opacity: 0;
        visibility: hidden;
        transition: 0.5s;

        & a {
            height: 50px;
            width: 50px;
            background-color:var(--it-theme-1);
            line-height: 50px;
            text-align: center;
            border-radius: 50%;
            display: inline-block;
            font-weight: 300;
            color: var(--it-common-black);
            margin: 0px 6px;
            transition: .3s;
            &:hover{
                background-color: #642cce;
                color:var(--it-common-white);
            }
        }
    }
    &__img-text {
        position: absolute;
        top: 0;
        right: 0;
        background-color: #642cce;
        padding: 8px 21px;
        border-radius: 20px;
        margin: 12px;

        @media #{$xl} {
            padding: 6px 17px;
        }

        @media #{$lg} {
            padding: 6px 15px;
        }

        @media #{$md} {
            padding: 6px 13px;
        }

        @media #{$xs} {
            padding: 6px 15px;
        }

        @media only screen and (min-width:440px) and (max-width:575px) {
            padding: 6px 15px;
        }

        & span {
            font-weight: 500;
            font-size: 17px;
            text-transform: uppercase;
            color: var(--it-common-white);

            @media #{$xl} {
                font-size: 15px;
            }

            @media #{$lg} {
                font-size: 15px;
            }

            @media #{$md,$xs} {
                font-size: 14px;
            }

            @media only screen and (min-width:440px) and (max-width:575px) {
                font-size: 15px;
            }
        }
    }
    &__content {
        padding: 30px 15px;
        box-shadow: rgba(0, 0, 0, 0.1) -4px 9px 25px -6px;
    }
    &__content-text {
        padding-bottom: 15px;

        & span {
            font-size: 17px;
            display: inline-block;
            padding-bottom: 7px;
        }

        & h4 {
            font-size: 22px;
            transition: .3s;
            &:hover{
                color: #642cce;
            }
            @media #{$xxl} {
                font-size: 20px;
            }

            @media #{$xl} {
                font-size: 19px;
            }

            @media #{$lg} {
                font-size: 18px;
            }

            @media #{$md,$xs} {
                font-size: 17px;
            }

            @media only screen and (min-width:440px) and (max-width:575px) {
                font-size: 21px;
            }
        }
    }
}
.product-details {
    &__plr {
        padding: 0 200px;

        @media #{$xxxl} {
            padding: 0 100px;
        }

        @media #{$xxl} {
            padding: 0 100px;
        }

        @media #{$xl,$lg,$md} {
            padding: 0 30px;
        }

        @media #{$xs} {
            padding: 0;
        }
    }
    &__img-box {
        padding-bottom: 100px;
        & img{
            padding: 0 15px;
        }
        @media #{$xs}{
            padding-bottom: 40px;
        }
    }
    &__list{
        padding-bottom: 20px;
        & ul {
            display: inline-block;
            padding-bottom: 5px;
    
            & li {
                list-style-type: none;
                position: relative;
                padding-left: 25px;
                margin-bottom: 10px;
                width: 25%;
                float: left;
        
                @media #{$md} {
                    width: 50%;
                }
                @media #{$xs} {
                    width: 100%;
                }
                @media #{$sm} {
                    width: 50%;
                }
    
                & i {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    font-weight: 500;
                    color: var(--it-common-blue-3);
                    font-size: 16px;
                }
    
                & span {
                    font-weight: 600;
                    font-size: 18px;
                    font-family:'Poppins', sans-serif;   
                    color: var(--it-common-black); 
                    @media #{$lg,$xs} {
                        font-size: 15px;
                    }
                    @media #{$md} {
                        font-size: 15px;
                    }
                }
            }
        }
    }
    &__text{
        padding-bottom: 20px;
        & p{
            font-size: 17px;
            @media #{$lg}{
                & br{
                    display: none;
                }
            }
            @media #{$md}{
                font-size: 15px;
                & br{
                    display: none;
                }
            }
            @media #{$xs}{
                font-size: 14px;
                & br{
                    display: none;
                }
            }
        }
    }
    &__text-2{
        padding-bottom: 70px;
        font-size: 17px;
        & p{
            @media #{$lg}{
                & br{
                    display: none;
                }
            }
            @media #{$md}{
                font-size: 15px;
                & br{
                    display: none;
                }
            }
            @media #{$xs}{
                font-size: 14px;
                & br{
                    display: none;
                }
            }
        }
    }  
    &__social-box{
        & span {
            font-size: 18px;
            margin-right: 10px;
        }
        & a{
            height: 33px;
            width: 33px;
            border: 1px solid var(--it-theme-1);
            display: inline-block;
            line-height: 33px;
            text-align: center;
            margin-right: 5px;
            transition: .3s;
            border-radius: 5px;
            & i{
                color: var(--it-theme-1);
                transition: .3s;
            }
            &:hover{
                background-color: var(--it-theme-1);
                border:1px solid transparent;
                & i{
                    color: var(--it-common-black);
                }
            }
        }
    }  
    &__left-title{
        font-size: 22px;
        font-weight: 700;
        padding-bottom: 10px;
        @media #{$md}{
            font-size: 19px;
        }
        @media #{$xs}{
            font-size: 18px;
        }
        @media #{$sm}{
            font-size: 20px;
        }
    }
    &__left-side{
        @media #{$xs}{
            padding-bottom: 40px;
        }
        & h5{
            font-size: 20px;
            font-weight: 700;
            & span {
                font-weight: 600;
                color: var(--it-common-blue-3);
                padding-bottom: 10px;
                display: inline-block;
            }
        }
        & p{
            font-size: 17px;
            & span {
                font-weight: 600;
                color: var(--it-common-black);
                & i{
                    color: var(--it-common-blue-3);
                    font-style: normal;
                }
            }
            @media #{$lg}{
                & br{
                    display: none;
                }
            }
            @media #{$md}{
                font-size: 15px;
                & br{
                    display: none;
                }
            }
            @media #{$xs}{
                font-size: 14px;
                & br{
                    display: none;
                }
            }
        }
    }
    &__star{
        padding-bottom: 20px;
        & span{
            font-size: 12px;
            color: var(--it-common-blue-3);
        }
        & i{
            font-size: 12px;
            color: var(--it-theme-1);
            &.color{
                color: #d5d5d5;
            }
        }
    }
    &__content{
        border-top: 1px solid var(--it-border-1);
        padding-top: 100px;
        @media #{$xs}{
            padding-top: 40px;
        }
    }
}


.pro-details-nav {
    background: #F9FAFB;
    display: inline-block;
  padding: 7px 7px;
}
.pro-details-nav-btn {
    border: none;
  & li {
    font-weight: 500;
    font-size: 18px;
    color: var(--it-text-body);
    position: relative;
    &.react-tabs__tab--selected {
        button {
            background: var(--it-common-white);
            color: var(--it-common-black);
        }
        &:after {
            display: none;
        }
    }
    & button{
      & span{
          position: relative;
          z-index: 9;
      }
      &::before {
          content: "";
          height: 100%;
          width: 102%;
          top: 0;
          left: -1px;
          background: #fff;
          position: absolute;
          z-index: 3;
          opacity: 0;
          visibility: hidden;
        }
        &.active::before {
          opacity: 1;
          visibility: visible;
      }
    }
    &::after {
      position: absolute;
      content: "";
      height: 40px;
      width: 1px;
      background: #DFE3E9;
      top: 50%;
      transform: translateY(-50%);
      @media #{$xs} {
        display: none;
      }
    }
    &:last-child {
        &::after {
            display: none;
        }
    }
    @media #{$xs} {
      font-size: 16px;
    }
    & .nav-links {
      padding: 20px 45px;
      border-radius: 5px;
      @media #{$md} {
        padding: 10px 20px;
      }
      @media #{$xs} {
        padding: 5px 15px;
      }
    }
  }
}
.tab-para {
  & p {
    font-size: 16px;
    line-height: 30px;
  }
}
.comments-box {
  margin-bottom: 50px;
}
.user-rating {
  & ul {
    & li {
      display: inline-block;
      color: #FFDC60;
    }
  }
}
.comments-text {
  & span , p {
      font-size: 14px;
      color: #777777;
      line-height: 26px ;
  }
}
.comment-title {
  & p {
    color: #777777;
    font-size: 16px;
  }
}
.comment-rating {
  & span {
    font-weight: 500;
    margin-right: 5px;
  }
  & ul {
    & li {
