}
.comment-input {
  & textarea {
    width: 100%;
    height: 120px;
    margin-bottom: 20px;
    border: 1px solid #dddd;
    padding: 20px;
    &:focus {
      outline: 0;
      border: 1px solid var(--it-theme-1);  
    }
  }
  & input {
    width: 100%;
    height: 50px;
    resize: none;
    outline: 0;
    border: 1px solid #dddd;
    padding:8px 20px;
    margin-bottom: 30px;
    &:focus {
      border: 1px solid var(--it-theme-1);
    }
  }
}
.comments-avatar {
    flex: 0 0 auto;
}
.table .add-info {
    font-size: 18px;
    font-weight: 500;
}
.table {
  & td {
    padding: 18px;
  }
}
.shop-left-right {
  @media #{$xxl,$xl,$lg,$md,$xs} {
    margin-left: 0;
    margin-right: 0;
  }
}
.it-comments-title{
  font-size: 24px;
  color: var(--it-common-black);
  font-weight: 700;
  text-transform: capitalize;
  @media #{$xs} {
    font-size: 18px;
  }
}
.avatar-name {
  & b {
      color: var(--it-common-black);
      font-weight: 700;
      text-transform: uppercase;
  }
}
.shop-faq {
  & .accordion-header {
    & button {
      font-size: 18px;
      padding: 20px 55px 20px 30px;
      @media #{$xs} {
        font-size: 14px;
      }
    }
  }
  & .accordion-content {
    & p {
      & br {
