@use '../utils' as *;

/*----------------------------------------*/
/*  21. service CSS START
/*----------------------------------------*/
 
.service{
    &__mlr{
        padding: 0 200px;
        @media #{$xxxl}{
            padding:0 100px;
        }
        @media #{$xxl}{
            padding:0 100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
        }
        @media #{$xs}{
            padding: 0;
        }
    }
    &__space{
        margin-top: -100px;
        padding-top: 200px;
    }
    &__space-2{
        padding-top: 120px;
        padding-bottom: 70px;
    }
    &__shape-1{
        position: absolute;
        right: 0;
        top: 20%;
    }
    &__shape-2{
        position: absolute;
        left: 0;
        top: 15%;
    }
    &__shape-3{
        position: absolute;
        right: 0;
        top: 10%;
        animation: tpupdown 0.8s infinite alternate;
        @media #{$lg}{
            top: 1%;
        }
        @media #{$md}{
            top: 2%;
        }
    }
    &__shape-4{
        position: absolute;
        left: 0;
        bottom:10%;
        animation: tpupdown 0.8s infinite alternate;
        @media #{$md}{
            bottom: 0;
        }
    }
    &__shape-5{
        position: absolute;
        left: 0;
        top: -9%;
        @media #{$xxxl}{
            top: -8%;
        }
        @media #{$xxl}{
            top: -7%;
        }
        @media #{$xl}{
            top: -6%;
        }
        @media #{$lg}{
            top: -3.5%;
        }
        @media #{$md}{
            top: -3%;
        }
        @media #{$xs}{
            top: -19px;
        }
        @media #{$sm}{
            top: -37px;
        }
        @media only screen and (min-width: 440px) and (max-width:575px){
            top: -27px;
        }
    }
    &__shape-6{
        position: absolute;
        left: 0;
        bottom: -6%;
        @media #{$xxxl}{
            bottom: -5%;
        }
        @media #{$xxl}{
            bottom: -5%;
        }
        @media #{$xl}{
            bottom: -4%;
        }
        @media #{$lg}{
            bottom: -2.5%;
        }
        @media #{$md}{
            bottom: -2%;
        }
        @media #{$xs}{
            bottom: -16px;
        }
        @media #{$sm}{
            bottom: -24px;
        }
        @media only screen and (min-width: 440px) and (max-width:575px){
            bottom: -20px;
        }
    }
    &__item{
        background-color: var(--it-common-white);
        border: 2px dashed transparent;
        transition: .3s;
        border-top-right-radius: 20%;
        padding: 70px 55px;
        @media #{$xl}{
            padding: 60px 46px;
        }
        @media #{$md}{
            padding: 50px 35px;
        }
        @media #{$xs}{
            padding: 40px 35px;
        }
        &.shape-none {
            .service__shape {
                display: none;
            }
        }
        &.bg-color{
            background-color: var(--it-common-black-2);
            border: 2px solid var(--it-border-2);
            & .service__title{
                color: var(--it-common-white);
                &.title-color-1{
                    &:hover{
                        color: var(--it-common-paste);
                    }
                }
                &.title-color-2{
                    &:hover{
                        color: var(--it-theme-1);
                    }
                }
                &.title-color-3{
                    &:hover{
                        color: var(--it-common-pink-2);
                    }
                }
                &.title-color-4{
                    &:hover{
                        color: var(--it-common-blue);
                    }
                }
                &.title-color-5{
                    &:hover{
                        color: var(--it-common-green);
                    }
                }
                &.title-color-6{
                    &:hover{
                        color: var(--it-common-pink-3);
                    }
                }
            }
            & .service__content{
                & p{
                    color: var(--it-common-white);
                }
            }
        }
        &:hover{
            border: 2px dashed var(--it-common-paste);
            & .service__icon-wrapper{
                transform:translateY(-10px);
            }
        }
    }
    &__border-2{
        &:hover{
            border-color:var(--it-theme-1);
        }
    }
    &__border-3{
        &:hover{
            border-color:var(--it-common-pink-2);
        }
    }
    &__border-4{
        &:hover{
            border-color:var(--it-common-blue);
        }
    }
    &__border-5{
        &:hover{
            border-color: var(--it-common-green);
        }
    }
    &__border-6{
        &:hover{
            border-color:var(--it-common-pink-3);
        }
    }
    &__shape{
        position: absolute;
        bottom: -30%;
        right: 8%;
    }
    &__icon-wrapper{
        display: inline-block;
        transition: .3s;
    }
    &__inner-icon{
        position: absolute;
        top: 41%;
        left: 55%;
        transform: translate(-50%, -50%);
        &.inner-hight{
            top: 41%;
            left: 52%;
