        & span{
            font-size: 40px;
            color: var(--it-common-white);
            font-weight: 700;
        }
    }
    &__title{
        font-weight: 700;
        font-size: 22px;
        padding-bottom: 10px;
        @media #{$xs}{
            font-size: 19px;
        }
        @media #{$sm}{
