        &.title-color-1{
            &:hover{
                color: var(--it-common-paste);
            }
        }
        &.title-color-2{
            &:hover{
                color: var(--it-theme-1);
            }
        }
        &.title-color-3{
            &:hover{
                color: var(--it-common-pink-2);
            }
        }
        &.title-color-4{
