        }
        &.color-12{
            color: var(--it-common-white);
            &:hover{
                color: #c6a477;
            }
        }
    }
    &__content{
        & p{
            padding-bottom: 5px;
            transition: .3s;
            @media #{$xs}{
                font-size: 14px;
            }
            @media #{$sm}{
                font-size: 15px;
            }
        }
    }
    &__link{
        & a{
            font-weight: 500;
            font-size: 17px;
            color: var(--it-common-paste);
            & i{
                margin-left:10px;
                transform: translateY(2px);
            }
            &:hover{
                & i{
                    animation: tfLeftToRight 0.4s forwards;
                }
