            color:var(--it-common-pink-3);
        }
    }
    &__item-bg{
        background-color: #31303f;
        border-radius: 40px;
        padding: 70px 65px;
        transition: .3s;
        &.item-hover-black{
            border-radius: 30px;
            padding: 70px 65px;
            border: 1px solid var(--it-border-1);
            transition: .3s;
            background-color: var(--it-common-white);
            transition: .3s;
        }
        @media #{$xl}{
            border-radius: 40px;
            padding: 50px 35px;
        }
        @media #{$md}{
            padding: 70px 32px;
        }
        @media #{$xs}{
            padding: 50px 35px;
        }
        @media #{$sm}{
            padding: 50px 50px;
        }
        &:hover{
            border:2px dashed transparent;
            background-color:#1d1d25;
            &.item-hover-black{
                background-color: var(--it-common-black);
                border:1px solid transparent;
                & .service__content{
                    & p{
                        color: var(--it-common-white);
                    }
                }
                & .service__title{
                    color: var(--it-common-white);
                }
            }
        }
    }
    &__plr{
        padding: 0 200px;
        padding-bottom: 50px;
        @media #{$xxxl}{
            padding:0 100px;
            padding-bottom: 50px;
        }
        @media #{$xxl}{
            padding:0 100px;
            padding-bottom: 50px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
            padding-bottom: 50px;
        }
        @media #{$xs}{
            padding: 0;
            padding-bottom: 50px;
        }
    }
    &__shape-1{
        position: absolute;
        top: -24%;
