    }
}
.service-2{
    &__plr{
        padding: 0 200px;
        @media #{$xxxl}{
            padding:0 100px;
        }
        @media #{$xxl}{
            padding:0 100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
        }
        @media #{$xs}{
            padding: 0;
        } 
    }
    &__space{
        padding-top: 110px;
        padding-bottom: 110px;
    }
    &__shape-1{
        position: absolute;
        left: 1.5%;
        top: 26%;
        animation: moving 9s linear infinite;
        @media #{$xxxl}{ 
            left: 0%;
        }
    }
    &__shape-2{
        position: absolute;
        left: 2%;
        bottom: 14%;        
        animation: jumpTwo 4s infinite linear;
        @media #{$xxxl}{ 
            left: 0%;
            bottom: 5%;
        }
    }
    &__shape-3{
        position: absolute;
        right: 0%;
        top: 7%;
        animation: tpupdown 0.8s infinite alternate;
        @media #{$xl}{    
            top: 1%;
        }
        @media #{$lg}{    
            top: 0;
        }
        @media #{$md}{    
            right: 0;
            top: 0;
        }
    }
    &__shape-4{
        position: absolute;
        right: 7%;
        top: 31%;
        animation: jumpTwo 4s infinite linear;
        @media #{$xxl}{    
            right: 3%;
        }
    }
    &__shape-5{
        position: absolute;
        right: 3%;
        bottom: 8%;        
        animation: jumpTwo 4s infinite linear;
        @media #{$xxxl,$xxl}{    
            bottom: 0;
        }
        @media #{$xl}{   
            right: 2%; 
            bottom: -1%;
        }
    }
    &__text{
        padding-right: 106px;
    }
    &__item-wrapper{
        background-color: var(--it-common-white);
        padding: 50px;
        border-radius: 10px;
        position: relative;
        margin-bottom: 30px;
        transition: .3s;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        @media #{$xxl}{          
            padding:50px 45px;
        }
        @media #{$xl}{          
            padding: 50px 36px;
        }
        @media #{$xs}{          
            padding: 25px 30px;
        }
        &:hover{
            background-color: var(--it-theme-1);
            &::after{
                background-color: var(--it-common-blue-4);
            }
            & .service-2__icon{
                & span{
                    & i{
                        color: var(--it-common-black);
                    }
                }
            }
            & .service-2__link{
                & a {
                    & i{
                        color: var(--it-common-blue-4);
                    }
                }
            }
        }
        &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            background-color: var(--it-common-blue-5);
            height: 100px;
            width: 100px;
            border-top-left-radius: 10px;
            transition: .3s;
            @media #{$xl}{          
                height: 94px;
                width: 86px;
            }
            @media #{$xs}{          
                height: 80px;
                width: 80px;
            }
        }
    }
    &__icon{
        margin-right: 30px;
        @media #{$xs}{          
            margin-bottom: 20px;
        }
        & span{
            height: 80px;
            width: 80px;
            background-color: var(--it-common-white);
            border-radius: 50%;
            line-height: 80px;
            box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07);
            display: flex;
            align-items: center;
            justify-content: center;
            &.icon-height{
                line-height: 95px;
            }
            &.icon-height-2{
                line-height: 90px;
                padding-left: 6px;
            }
            &.icon-height-3{
                line-height: 92px;
            }
            &.icon-height-4{
                line-height: 90px;
            }
            & i{
                font-size: 32px;
                color: var(--it-theme-1);
                font-weight: 500;
                transition: .3s;
                &.fa-signal-4 {
                    margin-top: -6px;
                    margin-right: -6px;
                }
            }
        }
    }
    &__title-sm{
        font-size: 22px;
        font-weight: 700;
        color: var(--it-common-black);
        padding-bottom: 5px;
        transition: .3s;
        &:hover{
            color: var(--it-common-blue-4);
        }
        @media #{$xs}{          
            font-size: 18px;
        }
    }
    &__link{
        & a {
            font-weight: 500;
            font-size: 17px;
            color: var(--it-common-black);
            transition: .3s;
            &:hover{
                color: var(--it-common-blue-4);
                & i {
                    animation: tfLeftToRight 0.4s forwards;
                  }
            }
            & i{
                padding-left: 10px;
                color: var(--it-theme-1);
                transition: .3s;
            }
        }
    }
    &__top-text{
        & p{
            padding-right: 106px;
            @media #{$xxxl}{          
                padding-right: 85px;
            }
            @media #{$xxl}{          
                padding-right:7px;
            }
            @media #{$xl}{          
                padding-right:0;
            }
            @media #{$lg,$md}{          
                padding-right:0;
                font-size: 15px;
            }
            @media #{$xs}{          
                padding-right:0;
                font-size: 14px;
            }
            @media #{$sm}{          
                padding-right:0;
            }
        }
    }
    &__content{
        & p{
            @media #{$xl}{   
                font-size: 15px;
            }
            @media #{$xs}{   
                font-size: 14px;
            }
        }
    }
    &__item {
        @media #{$xs}{  
            flex-wrap: wrap;
        }
      }
}

.service-details{
    &__plr{
        padding: 0 200px;
        @media #{$xxxl}{
            padding:0 100px;
        }
        @media #{$xxl}{
            padding:0 100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
        }
        @media #{$xs}{
            padding: 0;
        } 
    }
    &__service-item{
            background-color: #f7f9ff;

            padding: 47px 40px;
            margin-left: 20px;
            @media #{$xxl}{
                 padding: 15px 35px;
            } 
            @media #{$xl}{
                padding: 20px;
            } 
            @media #{$lg}{
                padding: 20px;
                margin-left: 0;
            } 
            @media #{$md}{
                margin-left: 0;
            } 
            @media #{$xs}{
                padding: 30px 15px;
                margin-left: 0;
            } 
        & h4 {
            font-weight: 700;
            font-size: 22px;
            padding-bottom: 20px;
            @media #{$lg}{
                padding-bottom: 10px;
            }
            @media #{$xs}{
                font-size: 19px;
                padding-bottom: 15px;
            }
        }
        & ul{  
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: space-between;  
            flex-wrap: wrap; 
        }
        & li{

                font-weight: 600;
                font-size: 16px;
                font-family: 'Poppins', sans-serif;
                color: var(--it-common-black);
                background-color: var(--it-common-white);
                list-style-type: none;
                margin-bottom: 20px;
                padding: 8px 12px;
                border-radius: 30px;
                transition: .3s;
                @media #{$xxl}{
                    font-size: 17px;
                    margin-bottom: 15px;
                } 
                @media #{$xl}{
                    font-size: 17px;
                    padding: 3px 12px;                    
                } 
                @media #{$lg}{
                    margin-bottom: 10px;
                    padding: 5px 12px;   
                    font-size: 15px;                 
                } 
                @media #{$xs}{
                    margin-bottom: 15px;
                    padding: 8px 12px;
                    font-size: 14px;                 
                } 
                &:last-child{
                    margin-bottom: 0;
                }
                &:hover{
                    background-color: var(--it-theme-1);
                }
                &::before{
                    content: '\f00c';
                    font-family: "Font Awesome 5 Pro";
                    background-color: var(--it-common-blue-4);
                    display: inline-block;
                    height: 30px;
                    width: 30px;
                    border-radius: 50%;
                    color: var(--it-common-white);
                    line-height: 30px;
                    font-size: 15px;
                    text-align: center;
                    margin-right: 20px;
                    @media #{$lg}{
                        height: 20px;
                        width: 20px;  
                        line-height: 20px;
                        font-size: 12px;              
                    }
                }
            }


            
        
    }
    &__list{
        padding-bottom: 75px;
        @media #{$md} {
            padding-bottom: 35px;
        }
        @media #{$xs} {
            padding-bottom: 25px;
        }
        & ul {
            display: inline-block;
            padding-bottom: 5px;
    
            & li {
                list-style-type: none;
                position: relative;
                padding-left: 25px;
                margin-bottom: 10px;
                width: 25%;
                float: left;
        
                @media #{$md} {
                    width: 50%;
                }
                @media #{$xs} {
                    width: 100%;
                }
                @media #{$sm} {
                    width: 50%;
                }
    
                & i {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    font-weight: 500;
                    color: var(--it-common-blue-3);
                    font-size: 16px;
                }
    
                & span {
                    font-weight: 600;
                    font-size: 18px;
                    font-family:'Poppins', sans-serif;   
                    color: var(--it-common-black); 
                    @media #{$lg,$xs} {
                        font-size: 15px;
                    }
                    @media #{$md} {
                        font-size: 15px;
                    }
                }
            }
        }
    }
    &__item{
        & p{
            padding-right: 190px;
            padding-bottom: 20px;
            @media #{$xxl}{
                padding-right: 0;
            } 
            @media #{$xl,$lg,$md}{
                padding-right: 0;
                font-size: 14px;
                & br{
                    display: none;
                }
                
            }
            @media #{$xs}{
                padding-right: 0;
                font-size: 14px;
                & br{
                    display: none;
                }
                
            }
        }
    }
    &__main-img{
        @media #{$md} {
            padding-bottom: 40px;
        }
        @media #{$xs} {
            padding-bottom: 35px;
        }
    }
    &__main-img-2{
        padding-bottom: 80px;
        @media #{$md} {
            padding-bottom: 40px;
        }
        @media #{$xs} {
            padding-bottom: 35px;
        }
    }
    &__section-box{
        padding-bottom: 80px;
        @media #{$md} {
            font-size: 15px;
            padding-bottom: 20px;
        }
        @media #{$xs} {
            font-size: 15px;
            padding-bottom: 20px;
        }
    }
    &__right-text{
        & p{
            @media #{$lg}{
                font-size: 15px;
            } 
            @media #{$md,$xs}{
                padding-bottom: 20px;
                font-size: 14px;
            } 
        }
    }
}
