        color: var(--it-common-red);
      }
    }
    @media #{$lg,$xs} {
      font-size: 19px;
    }
  }

  &__right-text {
    & p {
      color: var(--it-common-white-2);

      @media #{$lg} {
        font-size: 15px;
      }

      @media #{$xs} {
        font-size: 15px;
      }
    }
  }

  &__wrapper-space {
    margin-left: 200px;
    margin-right: 200px;
  }

  &__left-side {
    @media #{$lg,$md,$xs} {
      margin-bottom: 80px;
    }
  }

  &__wrapper-box {
    @media #{$lg} {
      margin-left: 50px;
    }
  }
}

.work-3__inner-wrapper {
  & .work-3__wrapper {
    background-color: var(--it-common-white);
    border: 1px dashed var(--it-theme-1);
    @media #{$sm} {
      max-width: none;
    }
    &:nth-child(2n + 1) {
      margin-left: 0;
    }
    & .work-3__section-title {
      @media #{$lg} {
        font-size: 18px;
      }
      @media #{$md} {
        font-size: 20px;
      }
    }
    & .work-3__item {
      border: none;
      background-color: var(--it-theme-1);
    }

    & .work-3__icon {
      @media #{$xxl} {
        margin-bottom: 20px;
      }
      @media #{$lg,$md,$xs} {
        margin-bottom: 25px;
      }
      & span {
        background-color: var(--it-common-white);

        & i {
          color: var(--it-theme-1);
        }
      }
    }

    & .work-3__right-text {
      & p {
        color: var(--it-common-black);
      }
    }

    & .work-3__section-title {
      color: var(--it-common-black);
    }
    &.inner-wrapper-2 {
      border: 1px dashed var(--it-common-blue);
      & .work-3__item {
        background-color: var(--it-common-blue);
      }

      & .work-3__icon {
        & span {
          & i {
            color: var(--it-common-blue);
          }
        }
      }
    }
    &.inner-wrapper-3 {
      border: 1px dashed var(--it-common-red);
      & .work-3__item {
        background-color: var(--it-common-red);
      }

      & .work-3__icon {
        & span {
          & i {
            color: var(--it-common-red);
          }
        }
      }
    }
    &.inner-wrapper-4 {
      border: 1px dashed var(--it-common-pink-3);
      & .work-3__item {
        background-color: var(--it-common-pink-3);
      }

      & .work-3__icon {
        & span {
          & i {
            color: var(--it-common-pink-3);
          }
        }
      }
    }
    &.inner-wrapper-5 {
      border: 1px dashed #6bf89c;
      & .work-3__item {
        background-color: #6bf89c;
      }

      & .work-3__icon {
        & span {
          & i {
            color: #6bf89c;
          }
        }
      }
    }
    & .work-3__content {
      flex-wrap: wrap;
    }
  }
}

.work-3__inner-wrapper {
  & .work-3__content {
    @media #{$xl} {
      flex-wrap: wrap;
      & .work-3__icon {
        & span {
          margin-bottom: 25px;
        }
      }
    }
  }
}
