@use '../utils' as *;

/*----------------------------------------*/
/*  26. video CSS START
/*----------------------------------------*/
.video{
    &__plr{
        padding: 130px 30px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @media #{$xs}{                
            padding: 130px 0;
        }
        @media #{$xxs}{                
            padding: 120px 0;
        }
    }
    &__content{
        & p{
            color: var(--it-common-white);
            padding-right: 80px;
            padding-bottom: 13px;
            @media #{$lg,$md}{   
                padding-right: 0;             
                & br{
                    display: none;
                }
            }
            @media only screen and (min-width: 440px) and (max-width:575px){            
                & br{
                    display: none;
                }
            }
            @media #{$xs}{                
                padding-right: 0;
            }
        }
    }
    &__icon{
        @media #{$lg,$md,$xs}{      
            margin-right: 0;
        }
        & a{
            font-size: 18px;
            height: 60px;
            width: 60px;
            border-radius: 50%;
            background-color: var(--it-common-white);
            line-height: 63px;
            padding-left: 6px;
            display: inline-block;
            text-align: center;
            color: #404040;
            position: relative;
            z-index: 1;
            &::after{
                content: '';
                position: absolute;
                height: 100%;
                width: 100%;
                top:50%;
                left: 50%;
                transform: translate(-50%,-50%);
                border-radius: 50%;
                background-color: rgba(255, 255, 255, 0.551);
                animation: borderanimate2 2s linear infinite;
                z-index: -1;
            }

        }
    }
    &__button {
        @media #{$xs}{      
            margin-bottom: 50px;
        }
        @media #{$xxs}{      
            margin-bottom: 0;
        }
      }
}
.video-2{
    &__bg{
