  }
  &__content-box {
    padding-top: 45px;
    padding-left: 60px;
    margin-left: 170px;
    background-color: var(--it-common-white);
    position: absolute;
    right: 0;
    bottom: -7%;
    @media #{$xxxl} {
      padding-right: 20px;
      padding-left: 50px;
    }
    @media #{$xxl} {
      padding-left: 30px;
      margin-left: 156px;
    }
    @media #{$xl} {
      padding-left: 25px;
      padding-right: 20px;
      margin-left: 92px;
    }
    @media #{$lg} {
      padding-top: 45px;
      padding-left: 40px;
      margin-left: 170px;
    }
    @media #{$md} {
      padding-top: 40px;
      padding-left: 27px;
      margin-left: 90px;
    }
    @media #{$xs} {
      padding-top: 20px;
      padding-left: 15px;
      margin-left: 67px;
      padding-right: 15px;
    }
    @media #{$sm} {
      padding-top: 54px;
      padding-left: 57px;
      margin-left: 180px;
    }
  }
  &__title-sm {
    font-size: 22px;
    font-weight: 700;
    padding-bottom: 10px;
    @media #{$xxl} {
      font-size: 20px;
    }
    @media #{$xl} {
      font-size: 20px;
    }
    @media #{$lg} {
      font-size: 20px;
    }
    @media #{$md} {
      font-size: 20px;
    }
    @media #{$xs} {
      padding-bottom: 0px;
      font-size: 15px;
    }
    @media #{$sm} {
      font-size: 19px;
    }
    &:hover {
      &.title-color-1 {
        color: #fcace9;
      }
      &.title-color-2 {
        color: var(--it-common-blue-4);
      }
      &.title-color-3 {
        color: #59c9da;
      }
    }
  }
  &__text {
    & p {
      @media #{$xl,$lg} {
        padding-right: 10px;
        font-size: 15px;
      }
      @media #{$md} {
        font-size: 15px;
        padding-right: 15px;
      }
      @media #{$xs} {
        font-size: 12px;
      }
      @media #{$sm} {
        font-size: 16px;
        padding-right: 30px;
      }
    }
  }
  &__category {
    margin-bottom: 20px;
    & span {
      font-size: 15px;
      color: var(--it-common-black);
      background-color: #fcace9;
      padding: 6px 28px;
      border-radius: 30px;
      @media #{$md} {
        padding: 5px 20px;
      }
      @media #{$xs} {
        font-size: 14px;
        padding: 4px 17px;
      }
      @media #{$sm} {
        font-size: 15px;
        padding: 5px 25px;
      }
    }
    &.category-color-2 {
      & span {
        background-color: var(--it-common-blue-4);
      }
    }
    &.category-color-3 {
      & span {
        background-color: #59c9da;
      }
    }
  }
  &__link {
    padding-bottom: 40px;
    @media #{$xs} {
      padding-bottom: 20px;
    }
    &.link-color-2 {
      & span {
        color: var(--it-common-blue-4);
      }
    }
    &.link-color-3 {
      & span {
        color: #59c9da;
      }
    }
    & span {
      font-size: 16px;
      font-weight: 500;
      color: #fcace9;
      @media #{$md} {
        font-size: 15px;
      }
      @media #{$xs} {
        font-size: 14px;
      }
      @media #{$sm} {
        font-size: 16px;
      }
      &:hover {
        & i {
          animation: tfLeftToRight 0.4s forwards;
        }
      }
      & i {
        padding-left: 10px;
      }
    }
  }
}

.project__section-arrow {
  position: relative;
  & .swiper-button {
    width: auto;
    position: unset;
    &:after {
      display: none;
    }
  }
  & button {
    height: 50px;
    width: 50px;
    background-color: var(--it-theme-1);
    border-radius: 50%;
    line-height: 53px;
    text-align: center;
    color: var(--it-common-black);
    margin: 0px 10px;
    transition: 0.4s;
    &:hover {
      transform: scale(1.2);
    }
  }
}
